import React, { Component } from "react";
import "../css/Recommendations.css";

import { Link } from "react-router-dom";

class Recommendations extends Component {
  state = {
    recommendations: [
      {
        id: 1,
        date: "2019-01-02",
        doctor: "dr Anna Nowak",
        content:
          "dieta niskokaloryczna, Lek XXX 200mg 2x1, Lek YYY 1x1, unikanie wysiku, kontrola za 2 tygodnie",
        attachments: ["receptaXXX", "receptaYYY"]
      },
      {
        id: 2,
        date: "2018-11-22",
        doctor: "dr Artur Bk",
        content: "zwolnienie z pracy 2 tyg, pi du偶o pyn贸w",
        attachments: ["zwolnienie-2211-0512"]
      },
      {
        id: 3,
        date: "2018-10-26",
        doctor: "dr Joanna Lubelska",
        content: "zrobi USG serca",
        attachments: ["skierowanieUSG"]
      }
    ]
  };
  render() {
    return (
      <div className="container recommendations-container">
        {this.props.activeAccount === "doctor" && (
          <Link to="/recommendations/create-new">
            <button>Dodaj zalecenie</button>
          </Link>
        )}

        <div className="content">
          {this.state.recommendations.map((recommendation, i) => {
            return (
              <div className="recommendation" key={i}>
                <p>Data: {recommendation.date} </p>
                <p>Lekarz: {recommendation.doctor} </p>
                <p>Tre: {recommendation.content}</p>
                <p>
                  Zaczniki:{" "}
                  {recommendation.attachments.map((attachment, i) => (
                    <Link
                      key={i}
                      style={{
                        margin: "10px",
                        color: "white",
                        textDecoration: "underline"
                      }}
                      to={`/recommendations/:${attachment}`}
                    >
                      {attachment}
                    </Link>
                  ))}
                </p>
              </div>
            );
          })}
        </div>
      </div>
    );
  }
}

export default Recommendations;
